"use strict";

import Swiper from "./services/+esm";

document.addEventListener("DOMContentLoaded", () => {
    function changeDonecTextarea() {
        if (window.location.pathname === "/new.html" && window.innerWidth <= 1399) {
            const donecTextarea = document.querySelectorAll(".donec__textarea");
            donecTextarea[1].innerHTML =
                "Ut arcu tortor, hendrerit eget sollicitudin sed, tincidunt a lectus. Cras gravida ultricies ante sit amet consectetur. Maecenas sed arcu tempor, posuere odio pharetra, faucibus risus. Etiam fermentum felis quis aliquet viverra. Mauris in odio nec mi pellentesque placerat. Praesent at metus in lacus posuere molestie. Aliquam consequat, neque eget congue feugiat, lectus leo condimentum lorem, scelerisque euismod nisi ipsum nec metus. Mauris semper felis venenatis eros interdum sollicitudin.";
        }
    }

    window.addEventListener("resize", () => {
        changeDonecTextarea();
        changeThumbInputModal();
        changeTitleModal();
    });

    changeDonecTextarea();

    const swiper = new Swiper(".new-slider__swiper-container", {
        slidesPerView: "auto",
        loop: false,
        breakpoints: {
            1440: {
                spaceBetween: 48,
            },
            1024: {
                spaceBetween: 40,
            },
            768: {
                spaceBetween: 20,
            },
            375: {
                spaceBetween: 26,
            },
        },
    });

    const menu = document.querySelector(".menu");

    window.addEventListener("scroll", () => {
        window.scrollY >= 400
        ? (menu.style.background = "rgba(255, 255, 255, 0.8)")
        : (menu.style.background = "");
    });

    const requestBtn = document.querySelector(".menu__request");
    const body = document.querySelector("body");
    const modalClose = document.querySelector(".modal__close");

    const modal = document.querySelector(".modal");
    const spans = modal.querySelectorAll(".modal__form-range-labels span");

    const hamburger = document.querySelector(".hamburger");
    const bigMenu = document.querySelector(".menu__big-menu");

    const scrollbarWidth =
        window.innerWidth - document.documentElement.clientWidth;

    requestBtn.addEventListener("click", () => {
        modal.classList.add("modal_active");
        if (body.style.overflow === "hidden") {
            body.style.overflow = "";
            body.style.paddingRight = "";
        } else {
            body.style.overflow = "hidden";
            body.style.paddingRight = `${scrollbarWidth}px`;
        }
    });
    modalClose.addEventListener("click", () => {
        modal.classList.remove("modal_active");
        if (body.style.overflow === "hidden") {
            body.style.overflow = "";
            body.style.paddingRight = "";
        } else {
            body.style.overflow = "hidden";
            body.style.paddingRight = `${scrollbarWidth}px`;
        }
    });

    hamburger.addEventListener("click", () => {
        bigMenu.classList.toggle("menu__big-menu_active");
        hamburger.classList.toggle("hamburger_active");
        requestBtn.classList.toggle("menu__request_active");
        menu.classList.toggle("menu_modifikator");
        menu.style.background === "transparent"
        ? (menu.style.background = "rgba(255, 255, 255, 0.8)")
        : (menu.style.background = "transparent");
        if (window.innerWidth <= 1399 && window.scrollY >= 400) {
            menu.classList.toggle("menu_active");
        }

        if (body.style.overflow === "hidden") {
            body.style.overflow = "";
            body.style.paddingRight = "";
        } else {
            body.style.overflow = "hidden";
            body.style.paddingRight = `${scrollbarWidth}px`;
        }
    });

    // preloader
    const preloaderPercent = document.querySelector(".preloader__percent");
    const progressBar = document.querySelector(".preloader__progress-fill");
    let loadedResources = 0;
    let totalResources = 0;

    function updateProgress() {
        const percent = Math.floor((loadedResources / totalResources) * 100);
        preloaderPercent.textContent = `HI ${percent}%`;
        progressBar.style.width = `${percent}%`;
        setTimeout(() => {
            if (percent === 100) {
                preloaderPercent.textContent = `HILIGHT`;
            }
        }, 500);
    }

    function countResources() {
        totalResources =
            document.images.length +
            document.styleSheets.length +
            document.scripts.length;
    }

    function trackResourceLoading() {
        const checkResourcesLoaded = () => {
            loadedResources++;
            updateProgress();
        };

        const trackResource = (resource) => {
            resource.onload = checkResourcesLoaded;
            resource.onerror = checkResourcesLoaded;
        };
        [...document.images, ...document.styleSheets, ...document.scripts].forEach(
            trackResource
        );
    }

    countResources();
    trackResourceLoading();

    window.addEventListener("load", () => {
        loadedResources = totalResources;
        updateProgress();
        if (loadedResources === totalResources) {
            setTimeout(() => {
                document.querySelector(".preloader").style.display = "none";
            }, 1000);
        }
    });

    function changeThumbInputModal() {
        if (window.innerWidth < 1024) {
            const sliderOne = document.querySelector(
                ".modal__form-slider-one .modal__form-slider"
            );
            const sliderTwo = document.querySelector(
                ".modal__form-slider-two .modal__form-slider"
            );

            function toggleSliders(event) {
                console.log(1);
                if (event.target === sliderOne) {
                    sliderTwo.classList.add("hidden");
                    sliderOne.classList.remove("hidden");
                } else if (event.target === sliderTwo) {
                    sliderOne.classList.add("hidden");
                    sliderTwo.classList.remove("hidden");
                }
            }

            sliderOne.addEventListener("pointerdown", toggleSliders);
            sliderTwo.addEventListener("pointerdown", toggleSliders);
        }
    }

    const titleModal = document.querySelector(".modal__form-title");

    function changeTitleModal() {
        window.innerWidth <= 1399
        ? (titleModal.innerHTML = "Make request")
        : (titleModal.innerHTML = "Calculate the cost of work");
    }

    changeTitleModal();

    changeThumbInputModal();
});
